@charset "utf-8";
/* スイッチのスタイル */

.inner-container {
    display: flex;
    justify-content: center;
    align-items: center;  
    height: 40px;
}
.switch {
    position: relative;
    display: inline-block;
    width: 72px;
    height: 34px; 
    margin-left: 8px; 
  }

  
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;   
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: rgb(4, 47, 112);
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px  rgb(4, 47, 112);
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(36px);
    -ms-transform: translateX(36px);
    transform: translateX(36px);
  }
  
  /* スタイルの調整 */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }

  h4 {
    color: rgba(40, 147, 247, 0.772);
  }